/** Generated by FG **/

@font-face {
  font-family: "Conv_ofont.ru_Montserrat";

  src: url("fonts/ofont.ru_Montserrat.eot");

  src: local("O"), url("fonts/ofont.ru_Montserrat.woff") format("woff"),
    url("fonts/ofont.ru_Montserrat.ttf") format("truetype"),
    url("fonts/ofont.ru_Montserrat.svg") format("svg");

  font-weight: normal;

  font-style: normal;
}

html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  font-family: "Montserrat";
  font-style: normal;
  font-size: 16px;
  line-height: 1.2;
  font-weight: normal;
  color: #1f2041;
}
.wrapper {
  max-width: 1160px;
  margin: 0 auto;
}

/*Header styles start*/

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: white;
}
.header_wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-top: 15px;
  padding-bottom: 15px;
}

.header_nav {
  margin-left: 284px;
}

.header_list {
  display: flex;
  flex-wrap: wrap;
}
.header_item {
  margin-right: 20px;
}
.header_item:last-child {
  margin-right: 0;
}
.header_link {
  font-size: 14px;
  line-height: 17px;
  color: rgba(31, 32, 65, 0.5);
  text-decoration: none;
}
.header_link:hover,
.header_link:focus,
.header_link:active {
  font-weight: bold;
  color: rgba(31, 32, 65, 0.75);
}
.header-button_login {
  width: 87px;
  height: 34px;
  border-radius: 22px;
  border: 2px solid #bc9cff;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #bc9cff;
  background-color: white;
}
.header-button_account {
  width: 196px;
  height: 34px;
  border-radius: 22px;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  border: 0;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
}

/*Header styles end*/

/*Intro styles start*/

.intro {
  height: 100vh;
  background: url(/img/intro-bg.jpg) no-repeat center;
  background-size: cover;
  padding-top: 70px;
}
.room-form {
  height: 380px;
  width: 374px;
  background-color: white;
  margin-top: 70px;
}
.room-form_wrap {
  max-width: 320px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 40px;
  margin-bottom: 30px;
  margin-left: 30px;
  margin-right: 30px;
  align-items: center;
}
.room-form_title {
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
}
.room-form_text {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  margin-bottom: 10px;
}
.room-form_field {
  border: 1px solid rgba(31, 32, 65, 0.25);
  box-sizing: border-box;
  border-radius: 4px;
  height: 44px;
  font-size: 14px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.75);
  padding-left: 15px;
}
.room-form_field_date {
  width: 150px;
}
.room-form_field_guest {
  width: 320px;
}
.room-form_field_wrap {
  max-width: 150px;
  padding-top: 20px;
}
.room-form_field_wrap_guest {
  max-width: 320px;
}
.room-form_button {
  width: 320px;
  height: 44px;
  margin-top: 30px;
  background: linear-gradient(180deg, #bc9cff 0%, #8ba4f9 100%);
  border-radius: 22px;
  border: 0;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
}

/*Footer styles start*/

.footer {
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: white;
}
.footer_wrapper {
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: space-between;
  padding-top: 100px;
  padding-bottom: 100px;
}
.footer_logo {
  margin-bottom: 20px;
}
.footer_slogan {
  max-width: 260px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
}
.footer_item {
  margin-bottom: 20px;
}
.footer_item:first-child {
  line-height: 15px;
}
.footer_link {
  font-size: 14px;
  line-height: 17px;
  color: rgba(31, 32, 65, 0.5);
  text-decoration: none;
}
.footer_link:hover,
.footer_link:focus,
.footer_link:active {
  font-weight: bold;
  color: rgba(31, 32, 65, 0.75);
}
.footer_link-great {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1f2041;
  text-decoration: none;
}
.footer-column_5 {
  max-width: 260px;
}
.title_subscribe {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 16px;
  text-transform: uppercase;
  color: #1f2041;
  margin-bottom: 20px;
}
.info_subscribe {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
  margin-bottom: 20px;
}
.form_subscribe {
  background: #ffffff;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
  width: 100%;
  height: 44px;
}
.footer_bottom {
  border-top: 1px solid rgba(31, 32, 65, 0.25);
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding: 23px 0 23px 0;
}
.footer_copy {
font-size: 14px;
line-height: 17px;
color: rgba(31, 32, 65, 0.75);
}
.footer_icons-item {
  height: 24px;
  width: 24px;
  margin-right: 20px;
}
.footer_icons-item:last-child {
  margin-right: 0;
}
/*Footer styles end*/
